@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

html {
  background-color: #272932;
}


/* background-color: #FDF500; */
.i {
    display: flex;
    height: 100vh;
  }
  
  .i-left {
    flex: 1 1;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .i-right {
    flex: 1 1;
    position: relative;
  }
  
  .i-left-wrapper {
    padding: 50px;
    height: 50%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  
  .i-intro {
    font-size: 30px;
    font-weight: 300;
  }
  
  .i-name {
    font-size: 60px;
  }
  
  .i-title {
    height: 50px;
    overflow: hidden;
  }
  
  .i-title-wrapper {
    height: 100%;
    -webkit-animation: move 10s ease-in-out infinite alternate;
            animation: move 10s ease-in-out infinite alternate;
  }
  
  @-webkit-keyframes move {
    25% {
      -webkit-transform: translateY(-50px);
              transform: translateY(-50px);
    }
    50% {
      -webkit-transform: translateY(-100px);
              transform: translateY(-100px);
    }
    75% {
      -webkit-transform: translateY(-150px);
              transform: translateY(-150px);
    }
    100% {
      -webkit-transform: translateY(-200px);
              transform: translateY(-200px);
    }
  }
  
  @keyframes move {
    25% {
      -webkit-transform: translateY(-50px);
              transform: translateY(-50px);
    }
    50% {
      -webkit-transform: translateY(-100px);
              transform: translateY(-100px);
    }
    75% {
      -webkit-transform: translateY(-150px);
              transform: translateY(-150px);
    }
    100% {
      -webkit-transform: translateY(-200px);
              transform: translateY(-200px);
    }
  }
  
  .i-title-item {
    height: 50px;
    font-size: 30px;
    font-weight: bold;
    color: #59b256;
    display: flex;
    align-items: center;
  }
  
  .i-scroll {
    width: 30px;
    height: 30px;
    position: absolute;
    bottom: 20px;
  }
  
  .i-img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    position: absolute;
  }
  
  .i-bg {
    -webkit-clip-path: polygon(100% 0%, 100% 51%, 100% 100%, 25% 100%, 0% 50%, 25% 0%);
            clip-path: polygon(100% 0%, 100% 51%, 100% 100%, 25% 100%, 0% 50%, 25% 0%);
    background-color: #59b256;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    right: 0;
  }
  
  @media screen and (max-width: 480px) {
    .i {
      flex-direction: column;
    }
  
    .i-left-wrapper {
    
      padding: 10px;
      align-items: center;
      justify-content: center;
      text-align: center;
      height: 100%;
    }
  
    .i-desc{
      display: none;
    }

    .i-img {
      object-fit: contain;
      }
    
  }
.a {
    height: 100vh;
    display: flex;
    align-items: center;
  }
  
  .a-left {
    flex: 1 1;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    height: 100%;
  }
  
  .a-right {
    flex: 1 1;
    /* color: antiquewhite; */
    padding: 50px;
  }
  
  .a-card.bg {
    position: absolute;
    top: 50px;
    left: 50px;
    background-color: #333;
  }
  
  .a-card {
    width: 60%;
    height: 70vh;
    border-radius: 30px;
    position: relative;
    overflow: hidden;
  }
  
  .a-img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: -webkit-transform 1s, -webkit-filter 2s ease-in-out;
    transition: transform 1s, filter 2s ease-in-out;
    transition: transform 1s, filter 2s ease-in-out, -webkit-transform 1s, -webkit-filter 2s ease-in-out;
    -webkit-filter: blur(2px);
            filter: blur(2px);
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
  }
  .a-img:hover {
    -webkit-filter: blur(0);
            filter: blur(0);
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  
  .a-award-img {
    width: 120px;
    height: 120px;
    border-radius: 20px;
  }
  
  .a-title {
    font-weight: 400;
  }
  
  .a-sub {
    margin: 20px 0px;
  }
  
  .a-desc{
      font-weight: 300;
  }
  
  .a-award{
      margin-top: 50px;
      display: flex;
      align-items: center;
      justify-content: space-between;
  }
  
  .a-award-texts{
      width: 70%;
  }
  
  .a-award-title{
      font-weight: bold;
      margin-bottom: 10px;
  }

  @media screen and (max-width:480px) {
    .a{
      flex-direction: column;
      text-align: center;
      margin-top: 50px;
    }

    .a-left {
      width: 100%;
    }

    .a-card {
      height: 30vh;

    }

    .a-card.bg, .a-award {
      display: none;
    }

    .a-right{
      padding: 20px;
    }
}
.pl{
    padding: 50px 100px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    /* color: azure; */
}

.pl-texts{
    width: 65%;
}

.pl-title{
    font-size: 50px;
    font-weight: 600;
}

.pl-desc{
    margin: 20px 0px;   
}

.pl-list{
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

span {
    font-size: inherit;
  }

  .ityped-cursor {
    -webkit-animation: blink 1s infinite;
            animation: blink 1s infinite;
  }

  @-webkit-keyframes blink {
      50%{
          opacity: 1;
      }
      100%{
          opacity: 0;
      }
    }

  @keyframes blink {
      50%{
          opacity: 1;
      }
      100%{
          opacity: 0;
      }
    }

    @media screen and (max-width: 480px) {
        .pl {
          padding: 10px;
        }
      
        .pl-texts {
          width: 100%;
        }
      
        .pl-desc {
          display: none;
        }

