          }
      }
.p{
    width: 30%;
    height: 40vh;
    margin: 20px 10px;
    border: 2px solid black;
    border-radius: 10px 10px 0px 0px;
    overflow: hidden;
}


.p-browser{
    height: 20px;
    background-color: rgb(8, 8, 8);
    display: flex;
    align-items: center;
}

.p-circle{
    width: 6px;
    height: 6px;
    border-radius: 50%;
    margin: 3px;
    background-color: white;
    transition: all 0.5s;
}


.p:hover{
    cursor: pointer;
}

.p-img{
    width: 100%;
    /* height: 100%; */
    transition: all 9s ease;
}

.p:hover  .p-circle{
    background-color: #FDF500;
}

.p:hover .p-img{
    -webkit-transform: translateY(-70%);
            transform: translateY(-70%);
}

@media screen and (max-width: 480px) {
    .p {
      width: 40%;
      height: 20vh;
    }
  }
.c{
    height: 100vh;
    position: relative;
}
.c-bg{
    width: 20px;
    height: 100%;
    background-color: #FDF500;
    position: absolute;
}

.c-wrapper{
    padding: 50px;
    display: flex;
}

.c-left{
    flex: 1 1;
}
.c-right{
    flex: 1 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.c-title{
    font-size: 60px;
    width: 80%;
}

.c-info-item{
    display: flex;
    align-items: center;
    margin: 50px 0px;
    font-weight: 300;
    width: 0%;
}

.c-icon{
    width: 30px;
    height: 30px;
    margin-right: 20px;
}

.c-desc{
    font-weight: 200;
}

form{
    margin-top: 20px;

}

input{
    width: 60%;
    height: 50px;
    border: none;
    border-bottom: 1px solid black;
    color: black;
    margin: 10px 0px;
    font-size: 14px;
    padding-left: 10px;
}

textarea{
    width: 100%;
    margin: 10px 0px;
    font-size: 14px;
    padding-left: 10px;
}

button{
    border: none;
    padding: 15px;
    font-size: 16px;
    font-weight: 600;
    background-color: #FDF500;
    cursor: pointer;
}

@media screen and (max-width: 480px) {
    .c-wrapper {
      flex-direction: column;
      padding: 0px 50px;
    }
  
    .c-title {
      font-size: 30px;
    }
  
    .c-info-item {
      margin: 20px 0px;
      width: 100%;
    }
  
    .c-desc {
      display: none;
    }
  
    form {
      margin-top: 0px;
      display: flex;
      flex-wrap: wrap;
    }
  
    input {
      width: 35%;
      height: 40px;
      margin: 10px;
      margin-left: 0;
    }
  
    button {
      margin-top: 10px;
    }
  }
.t{
    width: 50px;
    height: 25px;
    border-radius: 20px;
    border:  1px solid #999;
    background-color: white;
    position: fixed;
    top: 10px;
    right: 10px;
    z-index: 10;
    display: flex;
    align-items: center;
    justify-content: space-around;
    cursor: pointer;
}

.t-icon{
    width: 15px;
    height: 15px;
}

.t-button{
    width: 25px;
    height: 25px;
    border-radius: 50%;
    background-color: #999;
    position: absolute;
}
